.wpcf7 .wpcf7-not-valid-tip {
  font-weight: 400;
  font-size: 12px;
  color: #1C2021;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  height: 40px;
  top: 2px;
  left: 0;
  background-color: #fff;
  border: 1px solid #D9D9D9;
  box-shadow: 0px 4px 7px rgba(14, 13, 13, 0.16);
  padding: 8px 8px 8px 40px;
  width: auto;
  border-radius: 4px;
  background-image: url("../images/dist/form-err.png");
  background-repeat: no-repeat;
  background-position: 8px;
}
.wpcf7 .use-floating-validation-tip .wpcf7-not-valid-tip:before,
.wpcf7 .wpcf7-not-valid-tip:before {
  border-left: solid transparent 5px;
  border-right: solid transparent 5px;
  border-bottom: solid #fff 7px;
  top: -7px;
  content: " ";
  height: 0;
  left: 40px;
  position: absolute;
  width: 0;
}
.wpcf7 .use-floating-validation-tip .wpcf7-not-valid-tip:after,
.wpcf7 .wpcf7-not-valid-tip:after {
  border-left: solid transparent 7px;
  border-right: solid transparent 7px;
  border-bottom: solid #d9d9d9 8px;
  top: -8px;
  content: " ";
  height: 0;
  left: 40px;
  margin-left: -2px;
  position: absolute;
  width: 0;
  z-index: -1;
}
